import React from "react";
import "./Contact.css";
import { useForm, ValidationError } from "@formspree/react";
import 'bootstrap/dist/css/bootstrap.css';
import { Button } from 'react-bootstrap'
import { useState } from 'react'
import axios from 'axios';
function Contact() {
  const [state, handleSubmit] = useForm("xgerkwed");
  const [Email,setemail]=useState('')
    const [Note,setnote]=useState('')

    function handleInsert() {
      axios.post(
          "https://student-hr-backend.herokuapp.com/contact",
          {
            email:Email,
            note:Note
          }
          ).then((data)=>{
            window.location.reload()
        })
        }
  if (state.succeeded) {
    return (
      <div className="contact">
        <img
          height="100"
          width="100"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/ef/Emoji_u263a.svg/1200px-Emoji_u263a.svg.png"
          alt="smiling-emoji"
        />
        <br />
        <p>Thanks for your message! I will be contacting you if necessary</p>
      </div>
    );
  }
  return (
    <div className="contact">
      <form onSubmit={handleSubmit}>
        <label htmlFor="email">Email : </label>
        <input
          id="email"
          type="email"
          name="email"
          required
          placeholder="@example.com"
          onChange={(e)=>setemail(e.target.value)}
        />
        <ValidationError prefix="Email" field="email" errors={state.errors} />
        <br />
        <textarea
          required
          id="message"
          name="message"
          placeholder="Enter your message here"
          onChange={(e)=>setnote(e.target.value)}
        />
        <ValidationError
          prefix="Message"
          field="message"
          errors={state.errors}
        />
        <br />
        <Button className="mx-4" onClick={()=>{handleInsert()}}>Submit</Button>
      </form>
      <br />
      <div className="contact_text">
       <b>
        <p>
          This is a prototype website made as a project by&nbsp;
          <a href="https://www.linkedin.com/in/priyajit-mondal-a099551a0/">
            @Priyajit Mondal
          </a>
        </p>
        </b>
      </div>
    </div>
  );
}

export default Contact;
